    width: 80%;
    justify-content: center;
    margin: 0 auto;
}

.home_row {
    display: flex;
    z-index: 1;
    margin: 0 5px;
}

.home_img {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
}